.table{
    width: 100%;
    padding: 0rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 1.5rem;
  flex-direction: column;
  gap: 1rem;
}

.spinner {
  border: 6px solid #f3f3f3; 
  border-top: 6px solid #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-message{
    font-size: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #f3f3f3;
}